.play-video {
    flex-basis: 69%;
}

.play-video iframe {
    width: 100%;
    height: 37vw;
}

.play-video h3 {
    margin-top: 10px;
    font-size: 22px;
    font-weight: 600;
}

.playvideo-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 10px;
    font-size: 14px;
    color: #5a5a5a;
}

.playvideo-info img {
    width: 20px;
    margin-right: 8px;
}

.playvideo-info span {
    display: inline-flex;
    align-items: center;
    margin-left: 15px;
}

.play-video hr {
    border: 0;
    height: 1px;
    background: #ccc;
    margin: 10px 0;
}

.publisher {
    display: flex;
    align-items: center;
    margin-top: 20px;
}

.publisher div {
    flex: 1;
    line-height: 18px;
}

.publisher img {
    width: 40px;
    border-radius: 50%;
    margin-right: 15px;
}

.publisher div p {
    color: #000;
    font-size: 18px;
    font-weight: 600;
}

.publisher div span {
    font-size: 13px;
    color: #5a5a5a;
}

.publisher button {
    background: red;
    color: #fff;
    padding: 8px 30px;
    border: 0;
    outline: 0;
    border-radius: 4px;
    cursor: pointer;
}

.video-desc {
    padding-left: 55px;
    margin: 15px 0;
}

.video-desc p {
    font-size: 14px;
    margin-bottom: 5px;
    color: #5a5a5a;
}

.video-desc h4 {
    font-size: 14px;
    color: #5a5a5a;
    margin-top: 15px;
}

.comment {
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.comment img {
    width: 35px;
    border: 50%;
    margin-right: 15px;
}

.comment h3 {
    font-size: 14px;
    margin-bottom: 2px;
}

.comment h3 span {
    font-size: 12px;
    color: #5a5a5a;
    font-weight: 500;
    margin-left: 8px;
}

.comment-action {
    display: flex;
    align-items: center;
    margin: 8px 0;
    font-size: 14px;
}

.comment-action img {
    border-radius: 0;
    width: 20px;
    margin-right: 5px;
}

.comment-action span {
    margin-right: 20px;
    color: #5a5a5a;
}

@media (max-width: 900px) {
    .play-video {
        flex-basis: 100%;
    }

    .play-video iframe {
        width: 100%;
        height: 50vh;
    }

    .video-desc {
        padding-left: 0;
    }

    .play-video .playvideo-info span {
        margin-left: 0;
        margin-right: 15px;
        margin-top: 15px;
    }
    
}
